import React, { Component } from 'react';
import { EthereumService } from '../../services/EthereumService';
import { IItemsReceivedDetails, IQuestService } from '../../services/QuestService';
import { IQuestCharacter } from './Quests';
import { ICharacterDetails } from '../../models/CharacterDetails';
import { Character } from '../Characters/Character';
import { ICharacterAction } from '../../models/ICharacterAction';
import { ItemNftService } from '../../services/ItemNftService';
import { IItemDetails } from '../../models/ItemDetails';
import Flip from '../Common/Flip';
import { ITime } from '../../services/Interfaces';
import './Explorer.scss';
import unknownItem from '../../assets/unknown-item.png';
import landIcon from '../../assets/land.png';
import { Utils } from '../../services/Utils';

interface IExplorerProps {
  questService?: IQuestService;
  character?: ICharacterDetails;
  itemService?: ItemNftService;
  ethereumService?: EthereumService;
  onBeginQuest: (questCharacter: IQuestCharacter) => void;
}

interface IExplorerItems {
  received: IItemsReceivedDetails;
  item: IItemDetails;
}

interface IExplorerState {
  items: IExplorerItems[];
  questLength: number;
  questCountdownSecs: number;
  questCountdownTime: ITime;
  dropdownActiveClass: string;
}

export class Explorer extends Component<IExplorerProps, IExplorerState> {
  dropdownBody: React.RefObject<HTMLDivElement>;
  action1: ICharacterAction[] = [];
  action2: ICharacterAction[] = [];

  constructor(props: IExplorerProps) {
    super(props);

    this.dropdownBody = React.createRef();

    this.state = {
      items: [],
      questLength: 0,
      questCountdownSecs: 0,
      questCountdownTime: { days: '  ', hours: '  ', mins: '  ', secs: '  ', isComplete: false },
      dropdownActiveClass: ''
    };

    this.props.questService?.getQuestState(this.props.character!).then(quest => {
      this.updateTimeRemaining(quest[1]);
      this.setState({ questLength: quest[0] });

      if (!this.props.questService!.isPaymentApproved) {
        this.action1.push({
          name: 'Approve Corn Use',
          isApproved: true,
          onClick: (nft: ICharacterDetails) => this.aprovePayment(),
          onHoverMessage: 'Explorers need Corn to survive on a quest, you must approve the use of Corn by you Explorers'
        });
      } else if (this.state.questLength === 0) {
        this.action1.push({
          name: 'Begin Quest',
          isApproved: true,
          onClick: (character: ICharacterDetails) =>
            this.props.onBeginQuest({ questService: this.props.questService!, character: character }),
          onHoverMessage: null
        });

        this.action2.push({
          name: 'Release Explorer',
          isApproved: true,
          onClick: (nft: ICharacterDetails) => this.releaseExplorer(),
          onHoverMessage: null
        });
      } else if (this.state.questCountdownSecs <= 0) {
        this.action1.push({
          name: 'Complete Quest',
          isApproved: true,
          onClick: (nft: ICharacterDetails) => this.completeQuest(),
          onHoverMessage: `${
            this.props.character!.name
          } has returned exploring. Complete the quest to see what they discovered.`
        });
      } else {
        this.action1.push({
          name: 'Exploring',
          isApproved: true,
          onClick: () => {},
          onHoverMessage: `${
            this.props.character!.name
          } is out exploring. You must wait for the quest to end before being able to do anything else.`
        });
      }
    });

    this.props.questService?.getCharactersItemsReceived(this.props.character!).then(items => {
      var questItems: IExplorerItems[] = [];

      items.forEach(i => {
        var details = this.props.itemService!.itemMap.get(i.itemID.toNumber());
        questItems.push({
          item: details!,
          received: i
        });
      });

      this.setState({ items: questItems });
    });

    this.aprovePayment = this.aprovePayment.bind(this);
    this.releaseExplorer = this.releaseExplorer.bind(this);
    this.completeQuest = this.completeQuest.bind(this);
    this.toggleDropdown = this.toggleDropdown.bind(this);
  }

  updateTimeRemaining(deadlineBlockNumber: number) {
    const networkBlockTime = this.props.ethereumService!.networkManager!.network!.blocktime;

    const questCountdownSecs =
      networkBlockTime * (deadlineBlockNumber - this.props.ethereumService!.walletData.blockNumber);
    const secondsRemaining = questCountdownSecs < 0 ? 0 : Math.trunc(questCountdownSecs);

    const questCountdownTime = {
      days: secondsRemaining < 0 ? '00' : ('0' + Math.floor(secondsRemaining / (60 * 60 * 24))).slice(-2),
      hours: secondsRemaining < 0 ? '00' : ('0' + Math.floor((secondsRemaining / (60 * 60)) % 24)).slice(-2),
      mins: secondsRemaining < 0 ? '00' : ('0' + Math.floor((secondsRemaining / 60) % 60)).slice(-2),
      secs: secondsRemaining < 0 ? '00' : ('0' + (secondsRemaining % 60)).slice(-2),
      isComplete: secondsRemaining < 0
    };

    this.setState({ questCountdownSecs: questCountdownSecs, questCountdownTime: questCountdownTime });
  }

  aprovePayment() {
    this.props.questService?.approvePayment();
  }

  releaseExplorer() {
    this.props.questService?.releaseExplorer(this.props.character!);
  }

  completeQuest() {
    this.props.questService?.completeQuest(this.props.character!);
  }

  toggleDropdown(e: any) {
    if (this.dropdownBody!.current!.contains(e.relatedTarget)) return;

    this.setState({
      dropdownActiveClass: this.state.dropdownActiveClass === '' ? 'is-active' : ''
    });
  }

  tickerClass() {
    if (+this.state.questCountdownTime.days > 0) {
      return 'days';
    }
    if (+this.state.questCountdownTime.hours > 0) {
      return 'hours';
    }
    if (+this.state.questCountdownTime.mins > 0) {
      return 'mins';
    }
  }

  render() {
    return (
      <div className="explorer is-flex is-flex-direction-column is-justify-content-center">
        {this.props.questService!.isPaymentApproved && (
          <div className="quest-countdown mb-3">{this.renderCountDown()}</div>
        )}
        <Character
          actionsLv1={this.action1}
          actionsLv2={this.action2}
          onCardClick={() => {}}
          nft={this.props.character}
          isSample={false}></Character>
        <div className="quest-details">
          <React.Fragment>
            {this.state.items.length > 0 && (
              <div
                className="quest-items"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">Recent Quest Discoveries</div>
                <div className="last-quest">
                  {this.renderDiscoveredItem(this.state.items[this.state.items.length - 1])}
                </div>
              </div>
            )}
          </React.Fragment>
          <React.Fragment>
            {this.state.items.length > 1 && (
              <div
                className="quest-items mt-2"
                style={{
                  backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${
                    this.props.character!.image
                  })`
                }}>
                <div className="quest-item-title pt-2">Previous Quest Discoveries</div>
                <div className="quest-history">
                  {this.state.items.slice(0, this.state.items.length - 1).map((i, index) => (
                    <div key={index}>{this.renderDiscoveredItem(i)}</div>
                  ))}
                </div>
              </div>
            )}
          </React.Fragment>
        </div>
      </div>
    );
  }

  renderDiscoveredItem(ei: IExplorerItems) {
    return (
      <React.Fragment>
        <div className="quest-item">
          <img alt={ei.item?.name} className="token" src={ei.item?.imageUrl ?? unknownItem} />
          <span className="item-name">{ei.item?.name}</span>
          <span className="item-balance pl-2">{ei.received.itemAmount.toNumber()}</span>
        </div>
        <div className="quest-item">
          <div className="land-token">
            <img alt="LAND" className="token" src={landIcon} />
          </div>
          <span className="item-name">LAND</span>
          <span className="item-balance pl-2">{Utils.toFixed(Utils.formatUnits(ei.received.landAmount), 0)}</span>
        </div>
      </React.Fragment>
    );
  }

  renderCountDown() {
    return (
      <div className="countdown">
        {this.state.questCountdownSecs <= 0 && this.state.questLength === 0 && (
          <div className="quest-state">Explorer Idle</div>
        )}
        {this.state.questCountdownSecs <= 0 && this.state.questLength > 0 && (
          <div className="quest-state">Explorer Returned</div>
        )}
        {this.state.questCountdownSecs > 0 && (
          <div className="is-flex is-flex-direction-column">
            <div className="mb-2">Exploring</div>
            <div className="is-flex">
              <div className={'ticker ' + this.tickerClass()}>
                <div className="digits">
                  <Flip value={this.state.questCountdownTime.days} />
                </div>
                <div>
                  <div className="units">days</div>
                </div>
              </div>
              <div className={'ticker ' + this.tickerClass()}>
                <div className="digits">
                  <Flip value={this.state.questCountdownTime.hours} />
                </div>
                <div>
                  <div className="units">hours</div>
                </div>
              </div>
              <div className={'ticker ' + this.tickerClass()}>
                <div className="digits">
                  <Flip value={this.state.questCountdownTime.mins} />
                </div>
                <div>
                  <div className="units">mins</div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }

  renderActionDropDown() {
    if (this.state.questCountdownSecs > 0 && this.state.questLength > 0) {
      return <div></div>;
    } else {
      return (
        <div className={`action-dropdown dropdown ${this.state.dropdownActiveClass}`}>
          <div className="dropdown-trigger">
            <button
              className="button is-action"
              aria-haspopup="true"
              aria-controls="dropdown-menu"
              onBlur={this.toggleDropdown}
              onFocus={this.toggleDropdown}>
              <span>Action</span>
              <span className="icon is-small">
                <i className="fas fa-angle-down" aria-hidden="true"></i>
              </span>
            </button>
          </div>
          <div
            className="dropdown-menu"
            id="dropdown-menu"
            role="menu"
            ref={this.dropdownBody}
            onClick={this.toggleDropdown}>
            <div className="dropdown-content">
              {!this.props.questService!.isPaymentApproved && (
                <button className="button is-link is-transparent" onClick={this.aprovePayment}>
                  <span>Approve Corn Use</span>
                </button>
              )}
              {this.props.questService!.isPaymentApproved && this.state.questLength === 0 && (
                <button
                  className="button is-link is-transparent"
                  onClick={() =>
                    this.props.onBeginQuest({
                      questService: this.props.questService!,
                      character: this.props.character!
                    })
                  }>
                  <span>Begin Quest</span>
                </button>
              )}
              {this.state.questLength === 0 && (
                <button className="button is-link is-transparent" onClick={this.releaseExplorer}>
                  <span>Release Explorer</span>
                </button>
              )}
              {this.state.questCountdownSecs <= 0 && this.state.questLength > 0 && (
                <button className="button is-link is-transparent" onClick={this.completeQuest}>
                  <span>Complete Quest</span>
                </button>
              )}
            </div>
          </div>
        </div>
      );
    }
  }
}
