import { BigNumber } from 'ethers';
import { Utils } from '../services/Utils';

export interface IItemDetails {
  balance: number;
  id: number;
  price: BigNumber;
  maxSupply: BigNumber;
  name: string;
  imageUrl: string;
  animationUrl: string;
  paymentContract: string;
  mintingActive: boolean;
  itemType: number;
  rarity: number;
  durability: number;
  weight: number;
}

export async function toItemDetails(details: Array<any>): Promise<IItemDetails> {
  const imageUrl = details[4];
  const itemId = +imageUrl.split('/').pop().split('.')[0];
  return {
    balance: 0,
    id: itemId,
    price: details[0],
    maxSupply: details[1],
    name: details[2],
    imageUrl: details[3].replace('https://farmland-game.github.io/assets/', './assets/'),
    animationUrl: details[4].replace('https://farmland-game.github.io/assets/', './assets/'),
    paymentContract: details[5],
    mintingActive: details[6],
    itemType: +Utils.formatUnits(details[7], 0),
    rarity: +Utils.formatUnits(details[8], 0),
    durability: +Utils.formatUnits(details[9], 0),
    weight: +Utils.formatUnits(details[10], 0)
  };
}
